.cards_astro {
  background: url("../../images/astronautimg.jpg") center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  text-align: center;
  color: #fff;
  font-weight: normal;
  font-size: 55px;
  font-family: Arial, Helvetica, sans-serif;
}

h3 {
  text-align: center;
  color: #fff;
  font-size: 40px;
  font-weight: normal;
  font-family: Arial, Helvetica, sans-serif;
}

h2 {
  text-align: left;
  color: #fff;
  font-size: 55px;
  font-weight: normal;
  font-family: Arial, Helvetica, sans-serif;
}
h4 {
  text-align: left;
  color: #fff;
  font-size: 30px;
  font-weight: normal;
  word-wrap: break-word;
  font-family: Arial, Helvetica, sans-serif;
}
h5 {
  font-weight: 10px;
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
}
p {
  font-weight: 10px;
  font-size: 20px;
  color: #120e0d;
  font-family: Arial, Helvetica, sans-serif;
}
